import React, { useState, useEffect } from 'react';

// import { useHistory } from 'react-router-dom';

import sortTypes from '../../hoc/sortPokemonType/sortPokemonType';
import generationPokemons from '../../assets/generations.json';
import allpokemons from '../../assets/pokemonlist.json';
import usePrevious from '../../hoc/usePrevious/usePrevious';
import Pokemon from '../../components/Pokemon/Pokemon';
import './Pokemons.css';

import LoadMoreButton from '../../components/Button/LoadMoreButton/LoadMoreButton';
import Loading from '../../components/UI/Loading/Loading';

const OFFSET = 0;


const Pokemons = (props) => {

    const [pokemon, setPokemons] = useState([]);
    
    const [longLoadText, setLongLoadText] = useState(false);

    const [showBtn, setShowBtn] = useState(false);

    const [offset, setOffset] = useState(OFFSET);

    const prevFilter = usePrevious(props.filter);

    const prevSearchResult = usePrevious(props.searchResult);



    useEffect(() => {
        setShowBtn(false)
        props.setLoading(true);
        if (prevFilter !== props.filter) setPokemons([])


        if (props.filter) {
            if (props.filter !== prevFilter) setOffset(OFFSET)
            setPokemons([]);

            const fetchFilteredData = () => {
                
                if (props.filter.includes("type/")) {
                    setLongLoadText(true)

                    const type = props.filter.split("/").join("_");
                    const typeID = props.filter.split("/")[1].toLowerCase();

                    // localStorage.remove(type)
                    if (!localStorage.getItem(type)) {
                        const pokemons = sortTypes(typeID);
                        localStorage.setItem(type, JSON.stringify(pokemons));
                    }

                    const sortedPokemons = JSON.parse(localStorage.getItem(type))

                    setPokemons(
                        sortedPokemons.map(pokemon => {
                            return (
                                <Pokemon 

                                key = {pokemon.id}
                                name = {pokemon.name}
                                id = {pokemon.id}
                                pokemonType1 = {pokemon.types[0]}
                                pokemonType2 = {pokemon.types[1]} />
                            )
                        })
                    );
                    props.setLoading(false)
                    setLongLoadText(false)
                    
                    

                    

                }

                else if (props.filter.includes("generation/")) {
                    setLongLoadText(true)
                    const gen = props.filter.split("/").join("_");
                    const generationID = parseInt(props.filter.split("/")[1]);

                    if (!localStorage.getItem(gen)) {
                        let pokemons = []
                        generationPokemons.forEach((generation) => {
                            if (generation.generation === generationID) {
                                return pokemons = generation.pokemons;
                            }
                        })

                        localStorage.setItem(gen, JSON.stringify(pokemons));
                    }

                    const genPokemons = JSON.parse(localStorage.getItem(gen));

                    setPokemons(
                        genPokemons.map(pokemon => {
                            return (
                                <Pokemon 

                                key = {pokemon.id}
                                name = {pokemon.name}
                                id = {pokemon.id}
                                pokemonType1 = {pokemon.types[0]}
                                pokemonType2 = {pokemon.types[1]} />
                            )
                        })
                    );
                    props.setLoading(false)
                    setLongLoadText(false)

                }
               
            }

            fetchFilteredData();
        } else {

            const pokemonList = allpokemons;

            const end = offset+props.limit;

            const selectedPokemons = pokemonList.slice(offset, end)

            const pokemons = []
            selectedPokemons.forEach(pokemon => {
                pokemons.push(
                    <Pokemon 
                        key = {pokemon.id}
                        name = {pokemon.name}
                        id = {pokemon.id}
                        pokemonType1 = {pokemon.types[0]}
                        pokemonType2 = {pokemon.types[1]} />
        
                )
            })


            props.setLoading(false);
            setLongLoadText(false)

            pokemons.sort((a, b) => a.props.id - b.props.id);


            if (offset >= 1) {

                return setPokemons(prevState => {
                    if ((offset+props.limit) < allpokemons.length) setShowBtn(true)
                    return prevState.concat(pokemons);
                })
            } else {
                if ((offset+props.limit) < allpokemons.length) setShowBtn(true)
                return setPokemons(pokemons)
            }

            
        }
    }, [props.filter, offset])

    useEffect(() =>  {  
        if (props.searchResult && props.searchResult.length > 0 && props.searchResult !== "") {
            setShowBtn(false)

            props.setLoading(true);
            const pokemons = props.searchResult.map(pokemon => {
                const type1 = pokemon.types[0];
                const type2 = pokemon.types[1] ? pokemon.types[1] : null;
        
                return (
                    <Pokemon 

                        key = {pokemon.id}
                        name = {pokemon.name}
                        id = {pokemon.id}
                        pokemonType1 = {type1}
                        pokemonType2 = {type2} />
                )
            })

            setPokemons(pokemons)
            props.setLoading(false);
        } else if(prevSearchResult && props.searchResult.length <= 0) {
            setShowBtn(false)

            props.setLoading(true);
            const pokemons = <h2 style={{color: 'currentColor'}}> No Results Found.</h2>

            setPokemons(pokemons)
            props.setLoading(false);

        }
    }, [props.searchResult])

    const loadMoreHandler = () => {
        setOffset(prevState => {
            if ((prevState+props.limit) < allpokemons.length) {
                return prevState + props.limit;
            }
        })
    } 

    // window.onscroll = () =>{
    //     if (
    //         window.innerHeight + document.documentElement.scrollTop === document.documentElement.offsetHeight
    //     ) {
    //         scrollToEnd();
    //     }
    // }


    // pokemon = <Pokemon 
                        // name = "charizard"
                        // id = "6"
                        // pokemonType1 = 'fire'
                        // pokemonType2 = 'flying'
    //                     />;

    let loader = null;
    if (props.loading && offset >= 1) loader = <Loading addStyle={{top: "92.5%", width: '5rem', height: '5rem'}} />;
    else if (props.loading) loader = <Loading addStyle={{top: "50%"}}/>;
    return (
        <React.Fragment>
                <ul className={`Pokemons Pokemon__list ${props.darkMode ? "dark-color" : ""}`} style={props.loading ? {cursor: 'wait'} : null}>

                        {pokemon ? pokemon : null}

                </ul>

            {showBtn ? <LoadMoreButton clicked={loadMoreHandler} /> : null}


            {loader}
            {longLoadText ? (<h3 style={{
                textAlign: 'center',
                position: 'fixed',
                top: '65%',
                left: '50%',
                transform: 'translateX(-50%)'
            }}>Please wait...</h3>) : null}


        </React.Fragment>
    )
}

export default Pokemons



