import React, { useState } from 'react';
import { useHistory, useParams } from 'react-router-dom'

import Button from '../../../components/Button/Button';

import preLoaderImg from '../../../assets/images/dummy-image.png'

const FullPokemonCard = ({darkMode, name, cards}) => {
    const history = useHistory();
    const ID = useParams();

   const pokemonCardClasses = darkMode ? ["Pokemon__card", "Pokemon__card--light"] : ["Pokemon__card"]
   
   const pokemonCardSvgClasses = darkMode ? ["Pokemon__card-svg", "Pokemon__card-svg--light"] : ["Pokemon__card-svg"]

    const numOfCards = cards.length
    const [pageIndex, setPageIndex] = useState(1);

    const prevClickHandler = () => {
        if (pageIndex !== 1) {
            setPageIndex(prevState => prevState-1)
        }
    }

    const nextClickHandler = () => {
        if (pageIndex !== numOfCards) {
            setPageIndex(prevState => prevState+1)
        }
    }

    // const clickHandler = () => {
    //     ID.id = "";
    //     history.replace({pathname: "cards"})
    // }

    return (
        <div className={"main-content__body-item-3 " + pokemonCardClasses.join(" ")} style={ cards && cards.length > 0 ? {backgroundImage: `url('${cards[pageIndex - 1]}'), url('${preLoaderImg}')`} : null }>

            <h3 className="heading-3 Pokemon__card-title"> <span style={{textTransform: "capitalize"}}> {name} </span> cards</h3>
            

            {
                cards && cards.length > 0 ? 
                (
                    <div className="Pokemon__card-controls">

                        {
                            pageIndex !== 1 ?
                            <a onClick={prevClickHandler}>
                                <svg className={ pokemonCardSvgClasses.join(" ") + " Pokemon__card-svg-left"}>
                                    <use href="../img/sprints.svg#arrow-left-lg"/>
                                </svg> 
                            </a>
                            : null
                        }


                        {
                            pageIndex !== numOfCards ?
                                <a onClick={nextClickHandler}>
                                    <svg className={ pokemonCardSvgClasses.join(" ") + " Pokemon__card-svg-right"}>
                                        <use href="../img/sprints.svg#arrow-right-lg"/>
                                    </svg>
                                </a>

                            : null
                        }

                    </div>

                )   
                : <p className="heading-2" style={{
                    width: "100%",
                    height: "100%",
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center"
                }}
                >No cards found</p>
            }



            <Button path="/cards" additionalStyle={{
                width: '27rem',
                height: '6.69rem',
                position: 'absolute',
                left: '50%',
                bottom: '-2%',
                transform: 'translateX(-50%)',
                letterSpacing: '2px',
                wordSpacing: '3px'
            }}>Explore More Cards</Button>
        </div>
        )
}

export default FullPokemonCard



