import React from 'react';

import PokemonEvolutions from '../FullPokemonEvolutions/FullPokemonEvolutions';
import PokemonCard from '../FullPokemonCard/FullPokemonCard';

const FullPokemonMaininfo = ({darkMode, name, base, stats, id, flavorText, pokemonWeight, pokemonHeight, ability, generation, types, hasNoEvolutions, evolutions, cards}) => {
    const pokemonTypeClasses = darkMode ? ["Pokemon--types", "Pokemon--types--light"] : ["Pokemon--types"]
    const pokemonImgClasses = darkMode ? ["main-content__head__item-1", "main-content__head__item-1--light"] : ["main-content__head__item-1"]

    return (
    <React.Fragment>

            <div className="main-content__head">
                <div className={pokemonImgClasses.join(" ")} style={{ backgroundImage: `url("https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${id}.png")`}}>

                </div>
                <div className="main-content__head__item-2">
                    <p className="Pokemon__flavor_text title">
                        {flavorText}
                    </p>

                    <div className="Pokemon__main-info">
                        <div className="Pokemon__main-info-1">
                            <h3 className="heading-3">
                                Height
                            </h3>
                            <p className="base-text">
                                {(pokemonHeight/10).toFixed(1)}m
                            </p>
                        </div>
                        <div className="Pokemon__main-info-2">
                            <h3 className="heading-3">
                                Weight
                            </h3>
                            <p className="base-text">
                                {(pokemonWeight/10).toFixed(1)}kg
                            </p>
                        </div>
                        <div className="Pokemon__main-info-3">
                            <h3 className="heading-3">
                                Abilities
                            </h3>
                            <p className="base-text">
                                {ability}
                            </p>
                        </div>
                        <div className="Pokemon__main-info-4">
                            <h3 className="heading-3">
                                Generation
                            </h3>
                            <p className="heading-4">
                                {generation}
                            </p>
                        </div>
                    </div>
                </div>
            </div>


            <div className="main-content__body">
                <div className="main-content__body-item-1">
                    <div className="Pokemon__stats">
                        <h3 className="heading-3">Stats</h3>

                        <h2 className="heading-2">{base}</h2>

                        <div className="detail-stats">
                            <div className="detail-stats-row">
                                <span className="heading-4">HP</span>
                                <div className="stat-bar">
                                    <div className="stat-bar-bg" style={{width: `${(stats.hp / base) * 100}%`, borderRadius: (stats.hp / (base) * 100) > 95 ? "20px" : "20px 0 0 20px"  }}>
                                        <p className="regular-text--bold">{((stats.hp / base) * 100 ).toFixed(2)}%</p>
                                    </div>
                                </div>
                            </div>
                            <div className="detail-stats-row">
                                <span className="heading-4">Attack</span>
                                <div className="stat-bar">
                                    <div className="stat-bar-bg" style={{width: `${(stats.attack / base) * 100}%`, borderRadius: (stats.attack / (base) * 100) > 95 ? "20px" : "20px 0 0 20px"  }}>
                                        <p className="regular-text--bold">{((stats.attack / base) * 100 ).toFixed(2)}%</p>

                                    </div>
                                </div>
                            </div>
                            <div className="detail-stats-row">
                                <span className="heading-4">Defense</span>
                                <div className="stat-bar">
                                    <div className="stat-bar-bg" style={{width: `${(stats.defense / base) * 100}%`, borderRadius: (stats.defense / (base) * 100) > 95 ? "20px" : "20px 0 0 20px"  }}>
                                        <p className="regular-text--bold">{((stats.defense / base) * 100 ).toFixed(2)}%</p>

                                    </div>
                                </div>
                            </div>
                            <div className="detail-stats-row">
                                <span className="heading-4">Speed</span>
                                <div className="stat-bar">
                                    <div className="stat-bar-bg" style={{width: `${(stats.speed / base) * 100}%`, borderRadius: (stats.speed / (base) * 100) > 95 ? "20px" : "20px 0 0 20px"  }}>
                                        <p className="regular-text--bold">{((stats.speed / base) * 100 ).toFixed(2)}%</p>

                                    </div>
                                </div>
                            </div>
                            <div className="detail-stats-row">
                                <span className="heading-4">Sp. Attack</span>
                                <div className="stat-bar">
                                    <div className="stat-bar-bg" style={{width: `${(stats.special_attack / base) * 100}%`, borderRadius: (stats.special_attack / (base) * 100) > 95 ? "20px" : "20px 0 0 20px"  }}>
                                        <p className="regular-text--bold">{((stats.special_attack / base) * 100 ).toFixed(2)}%</p>

                                    </div>
                                </div>
                            </div>
                            <div className="detail-stats-row">
                                <span className="heading-4">Sp. Defense</span>
                                <div className="stat-bar">
                                    <div className="stat-bar-bg" style={{width: `${(stats.special_defense / base) * 100}%`, borderRadius: (stats.special_defense / (base) * 100) > 95 ? "20px" : "20px 0 0 20px"  }}>
                                        <p className="regular-text--bold">{((stats.special_defense / base) * 100 ).toFixed(2)}%</p>

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>                    

                    <div className={pokemonTypeClasses.join(" ")}>
                        <div>

                            <h3 className="heading-3 Pokemon--types-title">Type</h3>
                        
                            <div className="pokemon--type">
                                <div className="pokemon--type-1">
                                    <div className={"Type__circle Type--"+types.one} title={`${types.one} type`}>
                                        <svg>
                                            <use href={`../img/pokemon-types.svg#${types.one}-type`}></use>
                                        </svg>
                                    </div>
                                    <p className="base-text">
                                        {types.one}
                                    </p>
                                </div>
                                
                                {
                                    types.two ?
                                    (
                                        <div className="pokemon--type-2">
                                            <div className={`Type__circle Type--${types.two}`} title={`${types.two} type`}>
                                                <svg>
                                                    <use href={`../img/pokemon-types.svg#${types.two}-type`}></use>
                                                </svg>
                                            </div>
                                            <p className="base-text">
                                                {types.two}
                                            </p>
                                        </div>
                                    )
                                    : null
                                }
                            </div>
                        </div>


                        <div className="pokemon-weak-against">

                            <h3 className="heading-3 pokemon-weak-against-title">Weak against</h3>


                            <div>

                                {
                                    types.weakAgainst.length > 0 ?
                                        types.weakAgainst.map(type => (
                                            <div key={id+type} className={`Type__circle Type--${type}`} title={`${type} type`}>
                                                <svg>
                                                    <use href={`../img/pokemon-types.svg#${type}-type`}></use>
                                                </svg>
                                            </div>
                                        ))
                                    : <p>&#8212;</p>
                                }

                            </div>
                        </div>


                        <div className="pokemon-strong-against">

                            <h3 className="heading-3 pokemon-strong-against-title">Strong against</h3>


                            <div>
                                {
                                    types.strongAgainst.length > 0 ?
                                        types.strongAgainst.map(type => (
                                            <div key={id+type} className={`Type__circle Type--${type}`} title={`${type} type`}>
                                                <svg>
                                                    <use href={`../img/pokemon-types.svg#${type}-type`}></use>
                                                </svg>
                                            </div>
                                        ))
                                    : <p>&#8212;</p>
                                }

                            </div>
                        </div>
                    </div>
                </div>


                {/* // Pokemon__evolutions */}
                <PokemonEvolutions
                    darkMode={darkMode}
                    name={name}
                    hasNoEvolutions={hasNoEvolutions}
                    evolutions={evolutions}/>
                

                {/* //Pokemon__card */}
                {
                    cards && cards.length > 0 ?
                    (
                        <PokemonCard 
                            darkMode={darkMode}
                            name={name}
                            cards={cards} />
                    )
                    : null
                }

                
        </div>
    </React.Fragment>
)}

export default FullPokemonMaininfo



