import pokemonList from '../../assets/pokemonlist.json';

const searchPokeon = (query) => {
    if (query && query.trim().length > 0) {
        const result = []
        pokemonList.map((pokemon) => {
            if (pokemon.name.toLowerCase().includes(query.trim().toLowerCase())) {
                result.push(pokemon);
            }
        })

        return result.slice(0, 20);
    } else {
        return [];
    }
}

export default searchPokeon



