import React from 'react';

import classes from './GoToTop.css';

const GoToTop = (props) => {
    const clicked = () => {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
    };

    return (
        <button className="goToTop" title="Go to top" onClick={clicked}>
            <svg className="goToTop-icon" viewBox="0 0 44 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M42 22L22 2L2 22" strokeOpacity="0.5" strokeWidth="4" strokeLinejoin="round"/>
            </svg>            
        </button>
    )
}

export default GoToTop



