import React, { useState, useEffect } from 'react';
import axios from '../../axios-cards';

import Loading from '../../components/UI/Loading/Loading';
import usePrevious from '../../hoc/usePrevious/usePrevious';
import FilterText from '../../components/FilterText/FilterText';
import LoadMoreButton from '../../components/Button/LoadMoreButton/LoadMoreButton';
import GoBack from '../../components/Button/GoBack/GoBack';
import Card from './Card/Card';
import SelectBox from '../../components//UI/Selectbox/Selectbox';
import SearchBox from '../../components/UI/SearchBox/SearchBox';
import './Cards.css';

const PAGE = 1;
const PAGE_SIZE = 20;

const Cards = ({darkMode}) => {
        document.title = "Pokémon Cards"

        let flag = 0;

        const [error, setError] = useState(false);

        const [cards, setCards] = useState([]);

        const [loading, setLoading] = useState(false);

        const [showList, setShowList] = useState(false);

        const [page, setPage] = useState(PAGE);

        const [pageCount, setPageCount] = useState(0);

        const [showBtn, setShowBtn] = useState(false);

        const [cardsfilterValue, setCardsFilterValue] = useState("");

        const [cardQueryValue, setCardQueryValue] = useState("");


        const prevFilterValue = usePrevious(cardsfilterValue);
        const prevQueryValue = usePrevious(cardQueryValue);


        useEffect(() => {
                if (flag < 1) {
                        if (prevFilterValue !== cardsfilterValue) {
                                setPage(1)
                                flag++
                        }
                }

                if (flag < 1) {
                        if (prevQueryValue !== cardQueryValue) {
                                setPage(1)
                                flag++
                        }
                }

                const fetchAsyncData = async() => {
                        setLoading(true);
                        setShowBtn(false);

                        try {
                                setError(false);
                                if (cardQueryValue) {
                                        const query = cardQueryValue;
        
                                        
                                                const response = await axios.get(`cards/?q=name:"${query}*"&pageSize=${PAGE_SIZE}&page=${page}`)
                
                                                const data = response.data;
                
                                                setPageCount(data.totalCount);
                
                                                const list = data.data;
                
                
                                                 const cards = list.map(card => {
                                                        return (
                                                                <Card 
                                                                        key={card.id+" "+card.name}
                                                                        name={card.name}
                                                                        img={card.images}
                                                                        text={card.flavorText} />
                                                        )
                                                })
                        
                                                if (page < Math.ceil(data.totalCount/PAGE_SIZE)) setShowBtn(true);
                        
                                                
                        
                                                setLoading(false);
                                                setShowList(true)
                
                                                if (cards.length <= 0) {
                                                        cards.push(<li style={{color: darkMode ? "#2a2a2a" : "#fafafa"}}>No Results Found.</li>)
                                                        setCards(cards)
                                                }
                                                if (page > 1) {
                                                        setCards(prevCards => prevCards.concat(cards))
                                                } else {
                                                        setCards(cards);
                                                }
        
                                        
                                        
        
        
                                } else if (cardsfilterValue) {
        
        
        
                                        
                                        const type = cardsfilterValue.split("/")[1];
        
                                        const response = await axios.get(`cards/?q=types:${type}&pageSize=${PAGE_SIZE}&page=${page}`)
        
                                        const data = response.data;
        
                                        setPageCount(data.totalCount);
        
                                        const list = data.data;
        
        
                                         const cards = list.map(card => {
                                                return (
                                                        <Card 
                                                                key={card.id+" "+card.name}
                                                                name={card.name}
                                                                img={card.images}
                                                                text={card.flavorText} />
                                                )
                                        })
                
                                        if (page < Math.ceil(data.totalCount/PAGE_SIZE)) setShowBtn(true);
                
                                        
                
                                        setLoading(false);
                                        setShowList(true)
        
                                        if (page > 1) {
                                                setCards(prevCards => prevCards.concat(cards))
                                        } else {
                                                setCards(cards);
                                        }
        
                                } else {
        
        
                                        const response = await axios.get(`cards?pageSize=${PAGE_SIZE}&page=${page}`)
                
                                        const data = response.data;
                
                                        setPageCount(data.totalCount)
                
                                        const list = data.data;
                
                                        const cards = list.map(card => {
                                                return (
                                                        <Card 
                                                                key={card.id+" "+card.name}
                                                                name={card.name}
                                                                img={card.images}
                                                                text={card.flavorText} />
                                                )
                                        })
                
                                        if (page < Math.ceil(data.totalCount/PAGE_SIZE)) setShowBtn(true);
                
                                        
                
                                        setLoading(false);
                                        setShowList(true)
        
                                        if (page > 1) {
                                                setCards(prevCards => prevCards.concat(cards))
                                        } else {
                                                setCards(cards);
                                        }
                                }

                        }
                        catch (error) {
                                setLoading(false);
                                setError(true)
                                console.log(error);
                        }
                        

                }   

                fetchAsyncData()
        }, [cardsfilterValue, cardQueryValue, page])


        const filterChangeHanlder = (e) => {
                flag=0;
                const value = e.target.value;
                setCardQueryValue("");
                setCardsFilterValue(value);
        }

        const searchFormSubmitHandler = (e) => {
                e.preventDefault();        
                flag=0;

                let value = e.target.elements.keyword.value
                
                setCardsFilterValue("")
                setCardQueryValue(value);
        }

        const removeFilterHandler = () => {
                flag=0;
                setCardQueryValue("");

                setCardsFilterValue("")
        };

        const loadMoreHandler = () => setPage(prevPage => {
                if (prevPage < Math.ceil(pageCount/PAGE_SIZE)) {
                        return prevPage+1
                }
        });




   
        // let mouseOverContainer = CardRef.current;
        // let ex1Layer = CardInnerRef.current;
        // console.log(ex1Layer);
            
        //     let onMouseMoveHandler = (e) => {
        //         if (e) {
        //                 setStopAnimation(false);
        //                 let inner = CardInnerRef.current;

        //                 let constrain = 115;
    
        //                 const transforms = (x, y, el) => {
        //                     let box = el.getBoundingClientRect();
        //                     let calcX = -(y - box.y - (box.height / 2)) / constrain;
        //                     let calcY = (x - box.x - (box.width / 2)) / constrain;
                            
        //                     return `perspective(100px) rotateX(${calcX}deg) rotateY(${calcY}deg)`;
        //                 };
                        
        //                 const transformElement = (el, xyEl) => {
        //                     setCardStyle(transforms.apply(null, xyEl));
        //                 }
                    
        //                 let xy = [e.clientX, e.clientY];
        //                 let position = xy.concat([inner]);
                        
        //                 window.requestAnimationFrame(() => {
        //                     transformElement(inner, position);
        //                 });
        //         } 
        //     };


        //     const onMouseLeaveHandler = () => setStopAnimation(true) 
            


        let loader = null;
        if (loading && page > 1) {
                if (!showList) setShowList(true)
                loader = <Loading addStyle={{zIndex: "999", top: "92.5%", width: '5rem', height: '5rem'} } />
        }
        else if (loading) {
                if(showList) setShowList(false)
                loader = <Loading addStyle={{top: "50%"}}/>
        };
    
        



        return (
                <div className='Cards'>
                        {loader}

                        { !loading ?
                                <React.Fragment>
                                <div className='Cards__head'>
                                        <div className='Cards__head-1'>
                                                <GoBack />
                                        </div>


                                        {
                                                !error ?
                                                (
                                                        <React.Fragment>
                                                                <div className='Cards__head-2'>

                                                                <SelectBox 
                                                                        widthClass="widthClass"
                                                                        filterVal={cardsfilterValue}
                                                                        filterValChangeHanlder={filterChangeHanlder}
                                                                        darkMode={darkMode} 
                                                                        disabled={false} >

                                                                        <optgroup label="Type:">
                                                                        <option className="Select-box__option base-text" value="type/colorless" >  
                                                                                colorless
                                                                        </option>
                                                                        <option className="Select-box__option base-text" value="type/darkness" >  
                                                                                darkness
                                                                        </option>
                                                                        <option className="Select-box__option base-text" value="type/dragon" >  
                                                                                dragon
                                                                        </option>
                                                                        <option className="Select-box__option base-text" value="type/fairy" >  
                                                                                fairy
                                                                        </option>
                                                                        <option className="Select-box__option base-text" value="type/fighting" >  
                                                                                fighting
                                                                        </option>
                                                                        <option className="Select-box__option base-text" value="type/fire" >  
                                                                                fire
                                                                        </option>
                                                                        <option className="Select-box__option base-text" value="type/grass" >  
                                                                                grass
                                                                        </option>
                                                                        <option className="Select-box__option base-text" value="type/lightning" >  
                                                                                lightning
                                                                        </option>
                                                                        <option className="Select-box__option base-text" value="type/metal" >  
                                                                                metal
                                                                        </option>
                                                                        <option className="Select-box__option base-text" value="type/psychic" >  
                                                                                psychic
                                                                        </option>
                                                                        <option className="Select-box__option base-text" value="type/water" >  
                                                                                water
                                                                        </option>
                                                                        </optgroup>


                                                                </SelectBox>

                                                                </div>
                                                                

                                                                <div className='Cards__head-3'>
                                                                <SearchBox 
                                                                        submittedSearchForm={searchFormSubmitHandler}
                                                                        placeholder="Search for Cards"
                                                                        darkMode={darkMode} />
                                                                </div>

                                                        </React.Fragment>
                                                )
                                                : null
                                        }

                                </div>

                                <h1 className='heading-2 Cards__title' style={{marginTop: "2rem", color: darkMode ? "#2a2a2a" : "#fafafa"}}>All Cards</h1>

                                <div className='Cards__filter' style={!cardsfilterValue ? {marginTop : 0} : {marginTop : "3rem"}}>
                                        {(cardsfilterValue && !loading) ? <FilterText type={ 
                                                cardsfilterValue.includes('type/') ? cardsfilterValue.split('/')[1] : cardsfilterValue } 
                                                conditionalWidth={cardsfilterValue.includes('type/') ? '15rem' : '20rem'}
                                                darkMode={darkMode} 
                                                clicked={removeFilterHandler}/> : null}

                                </div>


                                </React.Fragment>
                        : null}
                
                { 
                        !error ?
                        (
                                <React.Fragment>

                                                {
                                                        showList ?
                                                                <ul 
                                                                
                                                                className="Cards__list">
                                                                        
                                                                        {cards && cards.length > 0 ? cards : null}

                                                                </ul>
                                                        : null

                                                }

                                                {
                                                        showBtn ? <LoadMoreButton clicked={loadMoreHandler}/> : null
                                                }
                                </React.Fragment>
                        )
                        : <li style={{color: darkMode ? "#2a2a2a" : "#fafafa", listStyle: "none"}}>Unable to load cards from the server.</li>
                }

                

                </div>
        )
}

export default Cards



