import React from 'react';
import {Link} from 'react-router-dom';

import './Button.css';

const Button = (props) => (
    <Link target={props.newTab ? '_blank' : null} className="Pokemon__Button button-primary button--animation" to={props.path} onClick={props.clicked} style={props.additionalStyle ? props.additionalStyle : null} >
        {props.children}
    </Link>
)

export default Button



