import React from 'react';

const FullPokemonEvolutions = ({darkMode, name, hasNoEvolutions, evolutions}) => {
    const pokemonEvolutionClasses = darkMode ? ["Pokemon__evolutions", "Pokemon__evolutions--light"] : ["Pokemon__evolutions"]
    
    const pokemonEvolutionSvgClasses = darkMode ? ["Pokemon__evolutions-svg", "Pokemon__evolutions-svg--light"] : ["Pokemon__evolutions-svg"];
    return (
    <div className={"main-content__body-item-2 " + pokemonEvolutionClasses.join(" ")} >
        <h3 className="heading-3 Pokemon__evolutions-title">Evolutions</h3>

        {
            hasNoEvolutions ?
            (
                <div>
                    {
                        evolutions.first.name && evolutions.first.id ?
                        (
                            <React.Fragment>
                                <div className="Pokemon__evolutions-item-1">
                                    <img src={`https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${evolutions.first.id}.png`} alt={evolutions.first.name}/>    
                                    
                                    <h3 className="heading-3 Pokemon__evolutions-pokemon-name">{evolutions.first.name}</h3>
                                </div>

                                {   
                                    evolutions.second.name && evolutions.second.id ?
                                    (
                                        <React.Fragment>
                                            <svg className={pokemonEvolutionSvgClasses.join(" ")}>
                                                <use href="../img/sprints.svg#arrow-right"/>
                                            </svg>
                                
                                            <div className="Pokemon__evolutions-item-2">
                                                <img src={`https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${evolutions.second.id}.png`} alt={evolutions.second.name}/>    
                                                
                                                <h3 className="heading-3 Pokemon__evolutions-pokemon-name">{evolutions.second.name}</h3>
                                            </div>

                                            {
                                                evolutions.third.name && evolutions.third.id ?
                                                (
                                                    <React.Fragment>
                                                        <svg className={pokemonEvolutionSvgClasses.join(" ")}>
                                                            <use href="../img/sprints.svg#arrow-right"/>
                                                        </svg>
                                            
                                                        <div className="Pokemon__evolutions-item-3">
                                                            <img src={`https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${evolutions.third.id}.png`} alt={evolutions.third.name}/>    
                                                            
                                                            <h3 className="heading-3 Pokemon__evolutions-pokemon-name">{evolutions.third.name}</h3>
                                                        </div>
                                                    </React.Fragment>
                                                )
                                                : null
                                            }
                                        </React.Fragment>
                                    )
                                    : null
                                }
                            </React.Fragment>
                        ) :
                        null
                    }

        
        
                    
    
                </div>
    
            )
            : <p style={{marginTop: "2rem"}}> <span style={{textTransform: "capitalize"}}> {name} </span> has no evolutions</p>

        }
    </div>
)}

export default FullPokemonEvolutions



