import React from 'react';

import './FilterText.css';

const FilterText = (props) => {
    const filterClass = ["Filter", props.darkMode ? "Filter--light" : ""].join(" ")
    return (
        <div className={filterClass}> 
            <div className="Filter__box">
                <svg className="Filter__logo">
                    <use href={`../img/sprints.svg#filter${props.darkMode ? "--light" : ""}`} />
                </svg>
                                                
                <span className="regular-text">Filter</span>
            </div>
            <a className={"Type--" + props.type} style={{width: props.conditionalWidth}} onClick={props.clicked}>
                <span className="regular-text--bold">{props.type}</span>
                <svg className="Filter__logo-close">
                    <use href="../img/sprints.svg#close" />
                </svg>    
            </a>
        </div>
    )
}

export default FilterText



