import React from 'react';

import debounce from '../../../hoc/debounce/debounce';


const SearchBox = ({submittedSearchForm, keyDownSearchForm, inputRef, darkMode, placeholder}) => {
    const handleInput = (e, input) => !input ? keyDownSearchForm(e.target.value) : keyDownSearchForm(input);

    const debounced = debounce(handleInput)

    const searchInputClasses =  ["Search__input", darkMode ? "Search__input--light" : ""].join(" ");


    return (
        <a>
            <form className="Search" onSubmit={submittedSearchForm} ref={inputRef}>
                    <input 
                        type="search" list="Search--result" 
                        className={searchInputClasses} 
                        name="keyword" 
                        placeholder={placeholder}
                        onKeyUp={keyDownSearchForm ? (e => debounced(e)) : null}  
                          />
            </form>
        </a>
    )
}

export default SearchBox



