import React from 'react';

import GoBack from '../../../components/Button/GoBack/GoBack';

const limitName = (title, limit = 12) => {
    const newTitle = [];
    if (title.length > limit) {
     title.split(' ').reduce((acc, cur) => {
         const T = acc + cur.length;
         if( T <= limit) {
            newTitle.push(cur)
         }
         return T;
     }, 0)
     return `${newTitle.join(' ')}...`
    }
    return title;
};

const FullPokemonNavbar = ({darkMode, name, id}) => {
    const navBgClasses = darkMode ? ["Pokemon--Navigation__background", "Pokemon--Navigation__background--light"] : ["Pokemon--Navigation__background"];
    const navClasses = darkMode ? ["Pokemon--Navigation", "Pokemon--Navigation--light"] : ["Pokemon--Navigation"]

    return (
        <React.Fragment>
            <div className={navBgClasses.join(" ")}></div>
            <header className="Pokemon__header">
                <nav className={navClasses.join(" ")} title={`${name} ${(name && id ? "#" : null)} ${id}`}>
                    <GoBack />

                    <h1 className="heading-2 Pokemon__name" >{name && name.split("-").length > 1 ? name.split("-")[0]+"..." : name}   <span className="heading-2--light Pokemon--id">{(name && id ? "#" : null)}{String(id).length <=2 ? "0"+id : id}</span></h1>
                </nav>
            </header>
        </React.Fragment> 
    )
}

export default FullPokemonNavbar



