import pokemonList from '../../assets/pokemonlist.json';

const sortPokemonType = (type) => {
    const pokemons = [];

    pokemonList.forEach((pokemon) => {
        pokemon.types.map(pokemonType => {
            if (pokemonType && pokemonType.toLowerCase() === type) {
                pokemons.push(pokemon);
            }
        })
    })

    return pokemons
}

export default sortPokemonType



