import React, {useRef, useEffect} from 'react';

import SearchBox from '../../components/UI/SearchBox/SearchBox';
import SelectBox from '../../components/UI/Selectbox/Selectbox';
import Aux from '../../hoc/Aux/Aux';
import './Navbar.css';

const Navbar = (props) => {

        const inputRef = useRef(null);

        useEffect(() => {
                if (props.loading) {
                        inputRef.current.elements.keyword.value = "";
                }

        }, [props.loading])

        // darkMode
        const darkModeToggleClasses = ["Darkmode-toggle", props.darkMode ? "Darkmode-toggle--light" : ""].join(" ");
        const logoClasses = ["Navbar__logo", props.darkMode ? "Navbar__logo--light" : ""].join(" "); 
        const navBackClasses = ["Navbar__background", props.darkMode ? "Navbar__background--light" : ""].join(" ")
        // const selectBoxSelectclass =  props.darkMode ? "select--light" : "";
        const selectDropdownClasses =  ["Navbar__dropdown", props.darkMode ? "Navbar__dropdown--light" : ""].join(" ");
        // const searchInputClasses =  ["Search__input", props.darkMode ? "Search__input--light" : ""].join(" ");

        // const handleInput = (e, input) => !input ? props.keyDownSearchForm(e.target.value) : props.keyDownSearchForm(input);

        // const debounced = debounce(handleInput)
        return (
                <Aux>
                        <input type="checkbox" className={selectDropdownClasses}/>                            
                        <div className={navBackClasses}></div>
                        <header className="Header" >
                        <nav className="Navbar">
                                <a className="Logo_container" href="/" disabled={props.disabled} >
                                        <svg className={`Logo ${logoClasses}`} >
                                                <use href={`../img/sprints.svg#${window.innerWidth<=700 ? "simple-" : ""}logo${props.darkMode ? "-light" : ""}`}/>
                                        </svg>
                                </a>
                                <ul className="Navbar__list">
                                <li className="Navbar__item-1">
                                        <a>
                                        <input defaultChecked={!props.darkMode ? true : false} type="checkbox" className={darkModeToggleClasses} onClick={props.darkModeToggle} style={props.disabled ? {cursor: "not-allowed" } : null} disabled={props.disabled}/>
                                        </a>
                                </li>
                                <li className="Navbar__item-2">
                                        <SelectBox 
                                                disabled={props.disabled}
                                                // selectBoxSelectclass={selectBoxSelectclass}
                                                filterVal={props.filterVal}
                                                filterValChangeHanlder={props.filterValChangeHanlder}
                                                darkMode={props.darkMode} >

                                                <optgroup label="Cards">
                                                        <option className="Select-box__option base-text" value="cards">All Cards</option>
                                                </optgroup>
                                                
                                                <optgroup label="Type:">
                                                        <option className="Select-box__option base-text" value="type/normal" >  
                                                                normal
                                                        </option>
                                                        <option className="Select-box__option base-text" value="type/fighting" >  
                                                                fighting
                                                        </option>
                                                        <option className="Select-box__option base-text" value="type/flying" >  
                                                                flying
                                                        </option>
                                                        <option className="Select-box__option base-text" value="type/poison" >  
                                                                poison
                                                        </option>
                                                        <option className="Select-box__option base-text" value="type/ground" >  
                                                                ground
                                                        </option>
                                                        <option className="Select-box__option base-text" value="type/rock" >  
                                                                rock
                                                        </option>
                                                        <option className="Select-box__option base-text" value="type/bug" >  
                                                                bug
                                                        </option>
                                                        <option className="Select-box__option base-text" value="type/ghost" >  
                                                                ghost
                                                        </option>
                                                        <option className="Select-box__option base-text" value="type/steel" >  
                                                                steel
                                                        </option>
                                                        <option className="Select-box__option base-text" value="type/fire" >  
                                                                fire
                                                        </option>
                                                        <option className="Select-box__option base-text" value="type/water" >  
                                                                water
                                                        </option>
                                                        <option className="Select-box__option base-text" value="type/grass" >  
                                                                grass
                                                        </option>
                                                        <option className="Select-box__option base-text" value="type/electric" >  
                                                                electric
                                                        </option>
                                                        <option className="Select-box__option base-text" value="type/psychic" >  
                                                                psychic
                                                        </option>
                                                        <option className="Select-box__option base-text" value="type/ice" >  
                                                                ice
                                                        </option>
                                                        <option className="Select-box__option base-text" value="type/dragon" >  
                                                                dragon
                                                        </option>
                                                        <option className="Select-box__option base-text" value="type/dark" >  
                                                                dark
                                                        </option>
                                                        <option className="Select-box__option base-text" value="type/fairy" >  
                                                                fairy
                                                        </option>
                                                </optgroup>
                                                
                                                <optgroup label="Generation:">
                                                        <option className="Select-box__option base-text" value="generation/1">generation-1</option>
                                                        <option className="Select-box__option base-text" value="generation/2">generation-2</option>
                                                        <option className="Select-box__option base-text" value="generation/3">generation-3</option>
                                                        <option className="Select-box__option base-text" value="generation/4">generation-4</option>
                                                        <option className="Select-box__option base-text" value="generation/5">generation-5</option>
                                                        <option className="Select-box__option base-text" value="generation/6">generation-6</option>
                                                        <option className="Select-box__option base-text" value="generation/7">generation-7</option>
                                                        <option className="Select-box__option base-text" value="generation/8">generation-8</option>
                                                </optgroup>

                                        </SelectBox>
                                </li>
                                <li className="Navbar__item-3">
                                        <SearchBox 
                                                submittedSearchForm={props.submittedSearchForm}
                                                // searchInputClasses={searchInputClasses}
                                                keyDownSearchForm={props.keyDownSearchForm}
                                                placeholder="Search for Pokémon"
                                                inputRef={inputRef}
                                                darkMode={props.darkMode}
                                                disabled={props.disabled} />
                                </li>

                                </ul>
                        </nav>
                        </header>
                </Aux>
)};

export default Navbar;



