import React from 'react';

const SelectBox = (props) => {

    const selectBoxSelectclass =  props.darkMode ? "select--light" : "";


    return (
        <a className="Select-box__container" style={props.disabled ? {cursor: "not-allowed" } : null} disabled={props.disabled}>
            <label className={"Select-box "+props.widthClass} htmlFor="select"  >
                <select id="select" className={selectBoxSelectclass} required="required" value={props.filterVal} onChange={props.filterValChangeHanlder} disabled={props.disabled} style={props.disabled ? {cursor: "not-allowed" } : null}>
                    <option value="" defaultValue disabled>Filter</option>
                    
                    {props.children}                
                </select>
                <svg >
                    <use href={`../img/sprints.svg#arrow-down${props.darkMode ? "--light" : ""}`} />
                </svg>
                
            </label>  
        </a>
    )
}

export default SelectBox



