import React from 'react';
import {useHistory} from 'react-router-dom'

import DarkModeContext from '../../../context/darkModeContext';

import './GoBack.css';

const GoBack = (props) => {
    const history = useHistory();

    const onClickHandler = (setFilterVal, setInput, setSearchResult) => {
        setFilterVal("")
        setInput("")
        setSearchResult([])
        history.replace("/pokemons")
    }

    return (
        <DarkModeContext.Consumer>
            {(context) => (
                <div className='GoBack' onClick={() => onClickHandler(context.setFilterVal, context.setInput, context.setSearchResult)}>
                    <svg>
                            <use href={`../img/sprints.svg#arrow-left`} />
                    </svg>
                </div>

            )}
        </DarkModeContext.Consumer>
    )
}

export default GoBack



