import React from 'react';
import { useHistory } from 'react-router-dom';

import Button from '../Button/Button';
import './Pokemon.css';

import DarkModeContext from '../../context/darkModeContext';


const Pokemon = (props) => {
    // const context = useContext(DarkModeContext);
    const history = useHistory()

    ;

    return (
        <DarkModeContext.Consumer>
            {(context) => (
                <li className={context.darkmode ? "Pokemon__item Pokemon__item--light" : "Pokemon__item"}>
                
                <div className={"Pokemon__head-data Pokemon__head Type--" + props.pokemonType1}>
                    <h4 className="heading-4">{props.name}</h4>
                    <h2 className="Pokemon__id" style={{transform: String(props.id).length >=4 ? "rotate(-90deg) translate(-32%, -112%)" : "rotate(-90deg) translate(-50%, -112%)"}}>#{String(props.id).length <=2 ? "0"+props.id : props.id }</h2>
                    <img className="Pokemon__img" src={"https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/"+props.id+".png"} alt={"https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/"+props.id+".png" ? props.name : "Not found"} loading="lazy" />
                </div>

                <div className="Pokemon__body-data Pokemon__body">
                    <div className="Pokemon__types">
                        <div className={"Type__circle Type--" + props.pokemonType1} title={props.pokemonType1 + " type"}>
                            <svg>
                                <use href={"../img/pokemon-types.svg#" + props.pokemonType1 + "-type"} />
                            </svg>
                        </div>
                        
                        {
                            props.pokemonType2 ?
                                (<div className={"Type__circle Type--" + props.pokemonType2} title={props.pokemonType2 + " type"}>
                                    <svg>
                                        <use href={"../img/pokemon-types.svg#" + props.pokemonType2 + "-type"} />
                                    </svg>
                                </div>)
                            : null
                        }

                    </div>
                    <Button path={`${history.location.pathname}/${props.id}`}>More</Button>
                </div>
            </li>

            )}
        </DarkModeContext.Consumer>

            

    )
}

export default Pokemon



