import React from 'react';

import './Loading.css';

const Loading = (props) => {
    return (
        <div className="loader" style={props.addStyle ? props.addStyle : null}>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="-20 -20 40 40" style={{stroke: "#5F2EEA"}}>
                <circle r="15.9154943092" strokeWidth="5"></circle>
            </svg>
        </div>
    );
}

export default Loading;



