import React, {useState, useRef} from 'react';
import { motion } from "framer-motion"

import preloaderImg from '../../../assets/images/dummy-image.png'
import './Card.css';

const Card = (props) => {
    const CardRef = useRef(null);
    const CardInnerRef = useRef(null);

    const [showImg, setShow] = useState(false)

    const [stopAnimation, setStopAnimation] = useState(true);
    const [cardStyle, setCardStyle] = useState("");


    const onClickHandler = () => {setShow(prevState=> {
        if (!prevState === false) setStopAnimation(true);
        return !prevState
    })}

    const onMouseMoveHandler = (e) => {
        if (e) {
                setStopAnimation(false);
                let inner = CardInnerRef.current;

                let constrain = 100;

                const transforms = (x, y, el) => {
                    let box = el.getBoundingClientRect();
                    let calcX = -(y - box.y - (box.height / 2)) / constrain;
                    let calcY = (x - box.x - (box.width / 2)) / constrain;
                    
                    return `perspective(110px) rotateX(${calcX}deg) rotateY(${calcY}deg)`;
                };
                
                const transformElement = (el, xyEl) => {
                    setCardStyle(transforms.apply(null, xyEl));
                }
            
                let xy = [e.clientX, e.clientY];
                let position = xy.concat([inner]);
                
                window.requestAnimationFrame(() => {
                    transformElement(inner, position);
                });
        } 
    };


    const onMouseLeaveHandler = () => setStopAnimation(true) 
    

    return (
        <motion.li 
            layout
            className="Card" 
            onClick={onClickHandler}
            ref={CardRef}
            onMouseMove={onMouseMoveHandler}
            onMouseLeave={onMouseLeaveHandler}>
            {
                showImg ?
                <motion.div className="fullCard"
                initial={{opacity: 0}} 
                animate={{opacity: 1}}  >
                    <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M6 6L18.7742 18.7742" stroke="#fafafa" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                        <path d="M6 18.7742L18.7742 5.99998" stroke="#fafafa" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                    </svg>

                    <h2 className="heading-2">{props.name}</h2>
                        <motion.div
                            initial={{opacity: 0, y: "-100vh"}} 
                            animate={{opacity: 1, y:0, delay: 1}} 
                            // src={props.img.large} 
                            style={(showImg ? {opacity : "1"} : {opacity : "0"}), {backgroundImage: `url(${props.img.large}) , url(${preloaderImg})`}} 
                            className="fullCard__img"
                            // onLoad={() => <h4>Loading...</h4>} 
                        >

                        </motion.div>

                    <p className="regular-text">{props.text}</p>
                </motion.div>
                : null
            }
            <div id="inner" className={(stopAnimation || showImg) ? "stop__animation" : ""}
                style={cardStyle ? { transform: cardStyle } : null} 
                ref={CardInnerRef}>

                    <motion.div 
                        initial={{opacity: 0}}
                        animate={{opacity: 1}}
                        loading="lazy" 
                        className="Card__img"  
                        style={{ backgroundImage: `url(${props.img.small}), url(${preloaderImg})`}}>
                        <div className="overlay">
                            <svg version="1.1" className="Card__svg" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" x="0px" y="0px" width="60px" height="60px" viewBox="0 0 612 612" xmlSpace="preserve">
                                <path d="M535.5,0h-153v76.5h114.75c21.133,0,38.25,17.136,38.25,38.25V229.5H612v-153C612,34.253,577.747,0,535.5,0z M0,76.5v153
                                        h76.5V114.75c0-21.133,17.117-38.25,38.25-38.25H229.5V0h-153C34.253,0,0,34.253,0,76.5z M76.5,497.25V382.5H0v153
                                        C0,577.747,34.253,612,76.5,612h153v-76.5H114.75C93.617,535.5,76.5,518.383,76.5,497.25z M535.5,497.25
                                        c0,21.133-17.117,38.25-38.25,38.25H382.5V612h153c42.247,0,76.5-34.253,76.5-76.5v-153h-76.5V497.25z"/>
                            </svg>
                        </div>
                    </motion.div>
            </div>
        </motion.li>
    )
}

export default Card



