import React, { useState, useEffect } from 'react';
import { useHistory, useParams } from 'react-router-dom'
import axios from '../../axios';
import axiosCards from '../../axios-cards';

import Loading from '../../components/UI/Loading/Loading';
import Navbar from './FullPokemonNavbar/FullPokemonNavbar';
import PokemonMainContent from './FullPokemonMaininfo/FullPokemonMaininfo'
import classes from './FullPokemonDetails.css';

const BASE = 200;

const FullPokemonDetails = ({darkMode}) => {
    

    const history = useHistory();
    const ID = useParams().id;

    // state
    const [error, setError] = useState(false);

    const [loading, setLoading] = useState(false);
    const [base, setBase] = useState(BASE);

    const [pokemonInfo, setPokemonInfo] = useState({
        id: null,
        name: null,
        height: null,
        weight: null,
        ability: null,
        generation: null,
        flavorText: null,
        types: {
            one: null,
            two: null,
            weakAgainst: [],
            strongAgainst: [],
        },
        evolutions: {
            first: null,
            second: null,
            third: null,
        },
        cards: []
    })
    // const [pokemonName, setPokemonName] = useState(null);
    // const [pokemonHeight, setPokemonHeight] = useState(null);
    // const [pokemonWeight, setPokemonWeight] = useState(null);
    // const [pokemonAbility, setPokemonAbility] = useState(null);
    // const [pokemonId, setPokemonId] = useState(null);
    const [pokemonStats, setPokemonStats] = useState({
        hp: null,
        attack: null,
        defense: null,
        special_attack: null,
        special_defense: null,
        speed: null,
    });

    // const [pokemonflavortext, setPokemonflavortext] = useState(null);
    // const [pokemonGeneration, setPokemonGeneration] = useState(null);

    // fetching data 
    const fetchPokemonSpeciesData = async(id) => {
        const response = await axios.get(`pokemon-species/${id}`)
        const data = response.data;

        return data;
    }

    const fetchTypeData = async(type) => {
        const response = await axios.get(`type/${type}`)
        const data = response.data;

        return data;
    }

    const fetchEvolutionsData = async(id) => {
        const response = await axios.get(`evolution-chain/${id}`)
        const data = response.data;

        return data;
    }

    const fetchCards = async(name) => {
        const response = await axiosCards.get(`cards/?q=name:"${name.split("-")[0]}*"&pageSize=${10}&page=${1}`)
        const data = response.data;

        const imgType = window.innerWidth >= 800 ? "large" : "small"

        return data.data.map(card => card.images[imgType]);
    }

    useEffect(() => {
        const fetchData = async() => {
            setLoading(true)
            document.title = "Loading..."
            try {
                setError(false);

                const response = await axios.get(`pokemon/${ID}`)

                const data = response.data;

                // setting title
                document.title = data.name

                // base value
                data.stats.forEach((stats) => {
                    if (stats.base_stat > 200 && base != 300) setBase(300)   
                })

                // stats
                const stats = {};

                data.stats.forEach(stat => {
                    const key = stat.stat.name.split("-").join("_");
                    const value = stat.base_stat;
                    stats[key] = value;
                })

                // fetching more data
                // species data
                    const urlLen = data.species.url.split("/").length;
                    
                    const pokeId = data.species.url.trim().split("/")[urlLen - 2];
                    
                    const speciesData = await fetchPokemonSpeciesData(pokeId);
                // 

                // type data
                    const typeData = await fetchTypeData(data.types[0].type.name)
                // 

                // Evolution data
                    const evolutionLen = speciesData["evolution_chain"].url.split("/").length;
                        
                    const evolutionId = speciesData["evolution_chain"].url.trim().split("/")[evolutionLen - 2];

                    const evolutionData = await fetchEvolutionsData(evolutionId)
                // 
                
                // cards
                    const cards = await fetchCards(data.name);
                //

                // type information
                const weakAgainst = typeData.damage_relations.double_damage_from.map(type => type.name)
                const strongAgainst = typeData.damage_relations.double_damage_to.map(type => type.name)

                // evolution info
                const firstForm = {
                    name: evolutionData.chain.species.name, 
                    id: evolutionData.chain.species.url.split("/")[6]
                };

                const secondForm = {
                    name: evolutionData.chain.evolves_to[0] ? evolutionData.chain.evolves_to[0].species.name : null,
                    id: evolutionData.chain.evolves_to[0] ? evolutionData.chain.evolves_to[0].species.url.split("/")[6] : null
                };

                const thirdForm = {
                    name: evolutionData.chain.evolves_to[0] &&  evolutionData.chain.evolves_to[0].evolves_to[0] ? evolutionData.chain.evolves_to[0].evolves_to[0].species.name : null,
                    id : evolutionData.chain.evolves_to[0] &&  evolutionData.chain.evolves_to[0].evolves_to[0] ? evolutionData.chain.evolves_to[0].evolves_to[0].species.url.split("/")[6] : null
                };


                // setPokemonName(data.name);
                // setPokemonId(data.id);
                // setPokemonHeight(data.height);
                // setPokemonWeight(data.weight);
                // setPokemonAbility(data.abilities[0].ability.name);
                
                // setPokemonGeneration(speciesData.generation.name);
                // setPokemonflavortext(speciesData["flavor_text_entries"][0]["flavor_text"]);
                
                let flavorTxt; 
                
                speciesData["flavor_text_entries"].map((texts) => {
                    if (texts.language.name == "en") {
                        flavorTxt = texts.flavor_text;
                    }
                })


                setPokemonInfo({
                    id: data.id,
                    name: data.name,
                    height: data.height,
                    weight: data.weight,
                    ability: data.abilities[0].ability.name,
                    generation: speciesData.generation.name,
                    flavorText: flavorTxt,  
                    types: {
                        one: data.types[0].type.name,
                        two: data.types[1] ? data.types[1].type.name : null,

                        weakAgainst: [...weakAgainst],
                        strongAgainst: [...strongAgainst],
                    },
                    hasNoEvolutions: firstForm.name && secondForm.name ,
                    evolutions: {
                        first: firstForm,
                        second: secondForm,
                        third: thirdForm,
                    },
                    cards: [...cards]
                })
                
                setPokemonStats(stats);
                

                setLoading(false)



            }
            catch (error) {
                setError(true);
                setLoading(false);
                console.log(error);
            }
        }

        fetchData();
    }, [])


    return (
        <div className="Pokemon">

            {

                loading ?
                    <Loading />
                :
                    (
                        <React.Fragment>
                            <Navbar
                                darkMode={darkMode} 
                                name={pokemonInfo.name}
                                id={pokemonInfo.id}/>

                            
                            {
                                !error ?
                                (
                                    <React.Fragment>
                                        <main className="Pokemon__main-content">
                                            <PokemonMainContent 
                                                darkMode={darkMode} 
                                                name={pokemonInfo.name}
                                                base={base}
                                                stats={pokemonStats}
                                                id={pokemonInfo.id}
                                                flavorText={pokemonInfo.flavorText}
                                                pokemonWeight={pokemonInfo.weight}
                                                pokemonHeight={pokemonInfo.height}
                                                ability={pokemonInfo.ability}
                                                generation={pokemonInfo.generation}
                                                types={pokemonInfo.types}
                                                hasNoEvolutions={pokemonInfo.hasNoEvolutions}
                                                evolutions={pokemonInfo.evolutions}
                                                cards={pokemonInfo.cards} />
                                        </main>

                                        <footer className="credits" style={darkMode ? {color: "#000"} : null}>
                                            <p className="credit-text">Made with &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                    <svg width="25" height="24" viewBox="0 0 25 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                                <path d="M3.20765 3.77216C0.83443 6.13503 0.834432 9.96602 3.20765 12.3289L12.4126 21.4937L12.4759 21.4307L12.5392 21.4938L21.7441 12.329C24.1174 9.96609 24.1174 6.13511 21.7441 3.77223C19.3709 1.40936 15.5231 1.40936 13.15 3.77224L12.831 4.08978C12.6349 4.28505 12.317 4.28505 12.1208 4.08978L11.8018 3.77216C9.42859 1.40928 5.58085 1.40928 3.20765 3.77216Z" fill="#EB5757" stroke="#EB5757" strokeWidth="0.5" strokeLinecap="round" strokeLinejoin="round"/>
                                                </svg>
                                                by <span> <a href="http://instagram.com/xix3r" target="_blank">xix3r</a></span>. </p>
                                        </footer>

                                    </React.Fragment>
                                ) 
                                :
                                (
                                    <main className="Pokemon__main-content">
                                        <li style={{color: darkMode ? "#2a2a2a" : "#fafafa", listStyle: "none"}}>Unable to load Pokémon Data.</li>
                                    </main>
                                ) 
                            }
                        </React.Fragment>
                    )
            }
        </div>

    )
}

export default FullPokemonDetails



