import React from 'react';
import Button from '../Button';

const LoadMoreButton = (props) => (
    <Button path="#" clicked={props.clicked} 
            additionalStyle={{
                width: '20rem',
                height: '6rem',
                // position: 'absolute',
                left: '50%',
                transform: 'translateX(-50%)',
                letterSpacing: '2px',
                wordSpacing: '3px'
            }}>Load more
    </Button> 
)

export default LoadMoreButton



