import React, { useState } from 'react';
import { Route, Switch, Redirect, useHistory } from 'react-router-dom';

import searchPokemon from '../../hoc/searchPokemon/searchPokemon';
import FilterText from '../../components/FilterText/FilterText';
import Navbar from '../../components/Navbar/Navbar';
import Pokemons from '../Pokemons/Pokemons';
import Cards from '../Cards/Cards';
import FullPokemonDetails from '../FullPokemonDetails/FullPokemonDetails';

import GoToTopBtn from '../../components/Button/GoToTop/GoToTop';
import './Pokedex.css';

// context api
import DarkModeContext from '../../context/darkModeContext';


const Pokedex = (props) => {
    document.title = "Pokedex"

    const history = useHistory();

    // if (history.location.pathname === '/') history.push("/pokemons")

    const [loading, setLoading] = useState(false);
    

    const [input, setInput] = useState("");
    const [searchResult, setSearchResult] = useState([]);
    const [goToTop, setGoToTop] = useState(null);
    const [filterShowApprove, setFilterShowApprove] = useState(true);
    const [filterValue, setFilterValue] = useState("");
    const [darkMode, setDarkMode] = useState(false);


    const changeBodyColorLight = () => document.body.classList.add("body--light");
    const changeBodyColorDark = () => document.body.classList.remove("body--light");

    const filterChangeHanlder = (e) => {
        const value = e.target.value;
        if (value === 'cards') {
            return history.replace({pathname: value})
        }
        setFilterShowApprove(true)
        setFilterValue(value);
    }

    const searchFormSubmitHandler = (e) => {
        e.preventDefault();
        if (filterValue) setFilterShowApprove(false);
        
        let value = e.target.elements.keyword.value
        
        
        setInput(value);
        setSearchResult(value ? searchPokemon(value) : []);

        // e.target.elements.keyword.value = "";
    }

    const searchFormKeyDownHandler = (value) => {

        if (filterValue) setFilterShowApprove(false);
        // const value = e.target.value;
        
        // if(!value) {
            //     return history.push("/pokemons")
            // }
            
            setInput(value);
            setSearchResult(value ? searchPokemon(value) : []);
            
    }
    

    const removeFilterHandler = () => setFilterValue("");

    const darkModeToggleHandler = () => {
        setDarkMode(prevState => {
            if (!prevState) changeBodyColorLight()
            else changeBodyColorDark()
            
            localStorage.setItem("darkMode", !prevState)
            return !prevState;
        })

         

    }

    // Go To Top Button

    window.onscroll = () => {
        scrollFunction()
    };

    const scrollFunction = () => {
        setGoToTop((document.body.scrollTop > 1250 || document.documentElement.scrollTop > 1250) ? <GoToTopBtn /> : null)
    };

    return (
        <DarkModeContext.Provider value={{
            darkmode: darkMode,
            setFilterVal: setFilterValue,
            setInput: setInput,
            setSearchResult: setSearchResult
            }}>
        
        <Switch>

            <Route path="/pokemons/:id" exact>
                <FullPokemonDetails darkMode={darkMode}/>
            </Route>
                    
            <div className="Pokedex" >
                <Switch>

                    <Route path="/cards">

                        <Cards 
                            darkMode={darkMode}  />

                    </Route>

                    <Route path="/pokemons">

                        {
                            !loading ? (
                                <Navbar
                                    darkMode={darkMode}
                                    darkModeToggle={darkModeToggleHandler}
                                    loading={loading}
                                    input={input}
                                    disabled={loading} 
                                    submittedSearchForm={searchFormSubmitHandler}
                                    keyDownSearchForm={searchFormKeyDownHandler}
                                    filterVal={filterValue}
                                    filterValChangeHanlder={filterChangeHanlder} />
                            ) : null 
                        }


                        
                        <main className="Main-content">
                            {(filterValue && filterShowApprove && !loading) ? <FilterText type={ 
                                filterValue.includes('type/') ? filterValue.split('/')[1] : filterValue } 
                                conditionalWidth={filterValue.includes('type/') ? '15rem' : '20rem'}
                                darkMode={darkMode} 
                                clicked={removeFilterHandler}/> : null}


                            <Pokemons darkMode={darkMode} loading={loading} setLoading={setLoading} searchResult={searchResult} limit={20} filter={filterValue}/>

                        </main>
                        {goToTop}
                    </Route>


                    {/* <Route exact path="/"> */}
                        <Redirect from="/" to="/pokemons" />
                    {/* </Route> */}
                </Switch>

            </div>

        </Switch>

            
        </DarkModeContext.Provider>

    )
}

export default Pokedex;



